.information-block {
	position: relative;
	top: 250px;
	height:400px;
	left: 130px;
	width: calc(100vw - 260px);
}
.information-title {
	position: relative;
	top: 60px;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}

.information-text {
	position: absolute;
	width: 433px;
	height: 206px;
	padding-top: 90px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 27px;
	font-size: 15px;
	letter-spacing: 0.02em;
	color: #828282;
}

.page-numbers{
	position: absolute;
	height: 17px;
	display: flex;
	flex-direction: row;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	font-size: 14px;
	letter-spacing: 0.02em;
	color: #B2B2B2;
    margin-top: 340px;
}

.active {
	text-decoration-line: underline;
	color: #2D9CDB;
}
.controls {
	margin:7.5px;
	cursor: pointer;
}

.controls:not(.active):hover{
	color: #2D9CDB;
	opacity: 0.3;
}

.information-image-block {
	position: relative;
	width: 680px;
	height: 383px;
	float: right;
}
.information-image {
	position: relative;
    width: 680px;
    height: 383px;
	z-index: -1;
}
.information-image-text {
	position: absolute;
	bottom: 0px;
	width: 238px;
	height: 79px;
	background: rgba(45, 156, 219, 0.6);
	font-family: Elsie;
	font-style: normal;
	font-weight: normal;
	line-height: normal;
	font-size: 17px;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 20px;
}


