@media (min-width: 320px) and (max-width: 991px){
	.information-block {
		left: 10px;
		width: calc(100vw - 40px);
		top: 150px;
	}
	.information-title {
		left: 10px;
		font-size: 10vw;
   		width: 100vw;
	}
	.information-image-block  {
		display: none;
	}
	.information-text {
		position: relative;
		width: auto;
  	 	line-height: 30px;
   		font-size: 4vw;
   		height: 100%;
	}
	.page-numbers {
		width: auto;
		position: relative;
		align-items: center;
		font-size: 5vw;
		left:50%;
		margin: 0px;
	}
}

@media (min-width: 992px) and (max-width: 1239px){
	.information-block {
		left: 65px;
		width: calc(100vw - 130px);
	}
	.information-image-block {
		top:60px;
		width: auto;
		height: auto;
		float: right;
	}
	.information-image {
       	width: calc(55vw - 120px);
    	height: auto;
	}
}

@media (min-width: 1240px) and (max-width: 1399px){
	.information-block {
		left: 65px;
		width: calc(100vw - 130px);
	}
}

