.reviews-block {
	position: relative;
	height: auto;
	top: 400px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
}
.reviews-block-image-block{
	position: relative;
	width: 536px;
	height: 360px;
}
.reviews-block-image{
	position: relative;
}

.reviews-block-image-text{
	position: absolute;
	width: 194px;
	height: 60px;
	top: 300px;
    left: 342px;
	background: rgba(45, 156, 219, 0.6);
	font-family: Elsie;
	font-style: normal;
	font-weight: normal;
	line-height: normal;
	font-size: 15px;
	letter-spacing: 0.06em;
	text-transform: uppercase;
	color: #FFFFFF;	
	padding: 15px 10px 10px 20px;
}

.reviews-block-text-block{
	position: relative;
	left: 65px;
}
.reviews-block-title {
	top:20px;
	position: relative;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}
.reviews-block-text {
	top:50px;
	position: relative;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 26px;
	font-size: 15px;
	letter-spacing: 0.02em;
	color: #828282;
}
.reviews-block-sign {
	top:100px;
	position: relative;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 23px;
	font-size: 16px;
	color: #4F4F4F;
}

@media (min-width: 320px) and (max-width: 500px){
	.reviews-block{
		left: 10px;
		width: calc(100vw - 40px);
		top: 500px;
	}
	.reviews-block-text-block {
	    position: relative;
	    left: 10px;
	    width: 95vw;
	}
	.reviews-block-title {
		font-size: 10vw;
   		width: 100vw;
	}
	.reviews-block-text {
		position: relative;
		width: auto;
  	 	line-height: 30px;
   		font-size: 4vw;
   		height: 100%;
	}
	.reviews-block-sign {
    position: relative;
    width: 100px;
    line-height: 30px;
    font-size: 4vw;
    /* height: 100%; */
    top: -59px;
    /* word-wrap: break-word; */
    right: -37px;
}
	.reviews-block-image-block{
		width: auto;
		height: auto;
	}
	.reviews-block-image {
    position: absolute;
    width: 52vw;
    top: 268px;
    right: -360px;
    height: auto;
}
	.reviews-block-image-text{
		display: none;
	}
}
@media (min-width: 320px) and (max-width: 500px){

}

@media (min-width: 992px) and (max-width: 1219px){
	.reviews-block {
		left:65px;
		width:calc(100vw - 130px);
	}

.reviews-block-text-block{
		width: 300px;
		left: 0px;
	}
	.reviews-block-image-block{
		width: auto;
		height: auto;
	}
	.reviews-block-image{
	    width: 90%;
	    height: auto;
	}
	.reviews-block-image-text{
	     top: 264px;
   	 	left: 288px;
	}
	.reviews-block-title {
		top:10px;
	}
	.reviews-block-text {
		top: 20px;
	}
	.reviews-block-sign {
		top: 60px;
	}
}
@media (min-width: 1220px) and (max-width: 1399px){
	.reviews-block {
		left:65px;
		width:calc(100vw - 130px);
	}
	.reviews-block-text-block{
		float: right;
		width: 396px;
	}
}

@media (min-width: 1400px){
	.reviews-block {
		left:130px;
		width:calc(100vw - 260px);
	}

	.reviews-block-text-block{
		float: right;
		width: 396px;
	}
}
@media (min-width: 1900px){
	.reviews-block {
		left:530px;
		width:calc(100vw - 260px);
	}

	.reviews-block-text-block{
		float: right;
		width: 396px;
	}
