.partners-block {
	position: relative;	
 	left: 130px;  
	margin-top: 75px;
	display: flex;
	flex-direction: row;
	width: calc(100vw - 260px);
	justify-content: space-between;
}

@media (min-width: 320px) and (max-width: 991px){
	.partners-block {
		flex-flow: row wrap;
 		justify-content: space-evenly;
 		width:100vw;
 		left:0px;
 		margin-top: 20px;
 	}
	.partners-item img{
		width: 25vw;
		margin-left: 20px;
		margin-right: 20px;
		margin-top: 50px;
		height: auto;
	 	flex: 1 0 23%
	}
}

@media (min-width: 992px) and (max-width: 1399px){
	.partners-block {
		left: 65px;
		width: calc(100vw - 130px);
	}
}
@media (min-width: 1400px){
	.partners-item img{
		width: 10vw;
		height: auto;
	}
	}