.newpropeties-block{
    position: relative;
    left: 130px;
	top:120px;
	max-width: 100vw;
}

.newpropeties-header {
	position:relative;
	width: calc(100vw - 260px);
}

.newproperties-title {
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;
	letter-spacing: 0.02em;
	color: #09293C;
}

.newproperties-description{
	position: relative;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #828282;
}
.search-field{
	position: absolute;
    top: 3px;
    right: 0px;
}

.newproperty-search-group {
    position: absolute;
    right: 0px;
}
.newproperty-search {
	position: relative;
	width: 325px;
	border: 0;
	outline: 0;
	background: transparent;
	border-bottom: 1px solid #DDDDDD;
}
.newproperty-search-placeholder {
	position: relative;
	top:-25px;
	transition: all 200ms;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #CBCACA;
	user-select: none; 
}
.newproperty-search-placeholder:hover {
	  cursor: text;
}

.newproperty-search:focus + .newproperty-search-placeholder ,
.newproperty-search:valid + .newproperty-search-placeholder  {
  font-size: 60%;
  transform: translate3d(0, -100%, 0);
}

.search-button {
    position: relative;
}

.newproperty-carousel{
	position: relative;
	top:50px;	
	max-width: 90%;
}
.newproperties-card-body {
    padding: 0px;
    margin-top: 16px;
    margin-left: 16px;	
}
.card {
	width: 250px;
	height: 340px;
	border: none;
	cursor: pointer;
}

.card:hover {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.5);
	transform: scale(1.01);
}
.card:active {
	box-shadow: -1px -1px 35px -9px rgb(0,0,0, 0.7);
}
.CB9vby5YRbJWpv7hRrZ0x {
	height: 360px;
}
.card-img-top:hover {
		border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.card-img-top{

	display: block;
	width: 100% !important;
	height: 175px;

}
.card-title {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 21px;
	font-size: 14px;
	text-align: left;
	text-transform: uppercase;

	color: #4F4F4F;
}
.card-price {
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 35px;
	font-size: 24px;
	text-transform: uppercase;
	color: #2D9CDB;
	position: absolute;
	bottom: 0px;
	right: 20px;
}

.card-text {
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 19px;
	font-size: 12px;
	color: #000000;
}

@media (min-width: 320px) and (max-width: 500px){
	.newpropeties-block{
	    position: relative;
	    left: 10px;
	    width: calc(100vw - 20px);
		top:120px;
	}
	.newpropeties-header {
	    position: relative;
	    width: calc(100vw - 20px);
	}
	.newproperties-title {
	    font-size: 10vw;
	    width: 100vw;
	}
	.newproperties-description {
		top:30px;
	    font-size: 5vw;
	}
	.search-field {
		display: none;
	}
	.newproperty-carousel{
		position: relative;
		top: 40px;
		margin-left: auto;
		margin-right: auto;
   		max-width: 85%;

	}
	.card {
        width: 100%;
        height: 100%;
	}
	.newproperties-card-body {
	   height: 45vw;
	}
	.card-img-top {
	    height: 50vw;
	}
	.card-text {
		position: relative;
		white-space: normal; 
	}
	.CB9vby5YRbJWpv7hRrZ0x {
		padding-top:20px;
        height: calc(100vw + 60px);
	}
}
@media (min-width: 501px) and (max-width: 991px){
	.newpropeties-block {
	    position: relative;
	    left: 10px;
	    width: calc(100vw - 20px);
		top:120px;
	}
	.newpropeties-header {
		position:relative;
		left:25px;
		width: calc(100vw - 50px);
	}
	.search-field {
		display: none;
	}
	.newproperties-title {
		left: 10px;
	    font-size: 10vw;
	    width: 100vw;
	}
	.newproperties-description {
	    font-size: 3vw;
	}
	.newproperty-carousel {
		margin: auto;
	}
	.card {
        width: 100%;
 
	}
	.newproperties-card-body {
	   height: 45vw;
	}
	.card-img-top {
	    height: 17vw;
	}
	.card-text {
		position: relative;
		white-space: normal; 
	}
}
@media (min-width: 992px) and (max-width: 1219px){
	.newpropeties-block{
	    left: 65px;
		width: 100vw;
	}
	.card {
        width: 210px;
    	height: 310px;
	}
	.card-img-top {
	    height: 150px;
	}
}
@media (min-width: 1220px) and (max-width: 1399px){
	.newpropeties-block{
	    left: 65px;
	}
	.newpropeties-header {
		width: calc(100vw - 130px);
	}
	.newproperty-carousel {
		width: 100vw;
	}
}

@media (min-width: 1400px){
	.newpropeties-block{
	    left: 130px;
		max-width: 100vw;
	}
}