.logo {
	position: relative;
	width: 177.58px;
	height: 35.58px;
}.logo:hover {
	cursor: pointer;
	transform: scale(1.01);
	filter: invert(53%) sepia(100%) saturate(3103%) hue-rotate(166deg) brightness(100%) contrast(86%);
}
.navbar {
	height:80px;
	z-index: 2;
	position: absolute;
	width:100vw;
}
.navigation-button{
	position: absolute;
	width: 111px;
	height: 40px;
	top:19px;
	background: #2D9CDB;
	border-radius: 3px;
	border: none;
	cursor: pointer;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
}.navigation-button:hover{
    background: #2D9CDB;
}.navigation-button:active{
    box-shadow:inset 0 0 5px  #005987;
}

.navigation-link{
	/*position: relative;*/
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	color: #FFFFFF;
	/*margin-left: 19px;*/
	/*margin-right: 19px;*/
}.navigation-link:hover{
	color: #12A3DD;
	text-decoration: none;
}
@media (min-width: 320px) and (max-width: 991px){
	.logo {
		filter: invert(53%) sepia(100%) saturate(3103%) hue-rotate(166deg) brightness(100%) contrast(86%);
	}
	.navigation-link {
		color: black;
		font-size: 15px;
	}
	.navigation-button{
		position: relative;
	    left: 10px;
	    width: calc(100vw - 20px);
	    /*top:0px;*/
	    margin-bottom:0px;
	}
	.navbar-collapse{
		background-color: rgba(255,255,255,0.9);
	    width: 100vw;
	    left: 0px;
	    position: absolute;
	    top: 80px;
	}
	.navbar {
		background-color: transparent /*!important*/;
		border: none;
	}
	.navbar-dark .navbar-toggler{
		border-color: transparent;
	}
	.navbar-nav{
	      height: 80vh;
	    display: flex;
	    flex-direction: column;
	    justify-content: space-evenly;
	    text-align: center;
	}
	.navbar-block-item{
		position: relative;
	}
	.navbar-item {
		position: relative;
		display: flex;
		flex-direction: column;
	}
	.navbar-toggler {
  border: none;
  background: transparent !important;
}

.navbar-toggler:focus {
  outline: none;
  background: transparent !important;
}

.navbar-toggler .icon-bar {
  background-color: #fff;
  transform: rotate(0deg) translate(0px, 0px);
  transition: ease all .2s;
}

.navbar-toggler .icon-bar {
  display: block;
  width: 22px;
  height: 2px;
  border-radius: 1px;
}

.navbar-toggler .icon-bar+.icon-bar {
  margin-top: 4px;
}

.icon-bar:nth-child(2) {
  width: 16px;
  transition: ease all .2s;
}

.navbar-toggler:hover>.icon-bar:nth-child(2) {
  width: 22px;
  transition: ease all .2s;
}

.navbar-toggler:active>.icon-bar:nth-child(2) {
  width: 22px;
  transition: ease all .2s;
}

.navbar-toggler:not(.collapsed) .icon-bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 4px);
  transition: ease all .2s;
}

.navbar-toggler:not(.collapsed) .icon-bar:nth-child(2) {
  opacity: 0;
  transition: ease all .2s;
}

.navbar-toggler:not(.collapsed) .icon-bar:nth-child(3) {
  transform: rotate(-45deg) translate(4px, -4px);
  transition: ease all .2s;
}

}

@media (min-width: 992px) and (max-width: 1399px){
	.logo {
		left: 20px;
	}
	.navigation-button{
		right:50px;
	}
	.navbar-block-item {
		display: flex;
		flex-direction: row;
		position: relative;
		left: 64px;
	    width: calc(100vw - 468px);
	    justify-content: space-between;
	}
}

@media (min-width: 1400px){
	.logo {
		left: 120px;
	}

	.navigation-button{
		right:130px;
	}
	.navbar-block-item {
		display: flex;
		flex-direction: row;
		position: relative;
	    left: 172px;
	    width: calc(100vw - 675px);
	    justify-content: space-between;
	}
}

