.footer {
	position: relative;
	height:419px;
	display: flex;
	flex-direction: row;
	left:130px;
	width: calc(100vw - 260px);
	/*justify-content: space-evenly;*/
}

.footer-link{
	font-family: Roboto;
	font-style: normal;
	font-weight: 300;
	line-height: normal;
	font-size: 13px;
	text-transform: capitalize;
	color: #000000;
	margin-bottom: 14px; 
}.footer-link:hover{
	color: #2D9CDB;
	text-decoration: none;
}
.social-logo{
	margin-right:25px;
}
.social-logo:hover{
filter: invert(50%) sepia(49%) saturate(0%) hue-rotate(228deg) brightness(98%) contrast(93%);
}

.footer-logo{
	position: relative;
	width: 177.58px;
	height: 35.58px;
	margin-top: 80px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: normal;
	font-size: 10px;
	text-transform: capitalize;
	color: #D4D4D4;
}
.footer-services{
	position: relative;
	display: flex;
	flex-direction: column;
	margin-top: 95px;
	margin-left: 10%;
}

.footer-about{
	display: flex;
	flex-direction: column;
	position: relative;
	margin-top: 95px;
	margin-left: 10%;
}

.footer-customers{
	display: flex;
	flex-direction: column;
	position: relative;
	margin-top: 95px;
	margin-left: 10%;
}

.footer-social{
	position: relative;
	margin-top: 95px;
	margin-left: 10%;
}
.footer-social-logos{
	display: flex;
	flex-direction: row;
}
.footer-social-logos-mobile {
	display: none;
}
.footer-rules{

}
.footer-category-title{
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	text-transform: capitalize;

	color: #09293C;
}

.footer-rules{
	position: absolute;
	width: 322px;
	height: 15px;
	left: 559px;
	margin-top: 360px;
}

.rules-element {
	font-family: Roboto;
	font-style: normal;
	font-weight: normal;
	line-height: normal;
	font-size: 12px;
	text-transform: capitalize;
	color: #A0A0A0;
	margin-right: 12px;
}

.rules-element:hover {
	color: black;
	text-decoration: none;
}

@media (min-width: 320px) and (max-width: 991px){
	.footer {
		left:10px;
		width: 95vw;
		flex-wrap: wrap;
		/*justify-content: space-evenly;*/
		height: auto;
	}
	.footer-logo{
		display: none;
	}
	.footer-rules{
		position: relative;
		margin-top: 20px;
		left:20px;
	}
	.item {
	 	flex: 1 1 40%;
	}
	.footer-social {
		margin-top: 30px;

	}
	.footer-customers {
		margin-top: 30px;
	}
	.footer-social-logos{
		display: none
	}
	.footer-social-logos-mobile {
		display: flex;
		flex-direction: column;
	}
}
