@media (min-width: 992px) and (max-width: 1399px){
	.footer {
		left:65px;
		width: calc(100vw - 130px);
		/*justify-content: space-evenly;*/
	}
	.footer-rules{
		left:30vw;
	}
}
@media (min-width: 1400px){
	.footer-rules{
		left:30vw;
	}
}
@media (min-width: 1600px){
	.footer-rules{
		left:30vw;
	}
}