.bestprice-block{
	position: relative;
	height: auto;
	top: 320px;
	left: 130px;
	width: calc(100vw - 260px);
}
.bestprice-title {
	height: 48px;
	font-family: Hiragino Mincho Pro;
	line-height: normal;
	font-size: 32px;	
	letter-spacing: 0.02em;
	color: #09293C;
}
.bestprice-text{
	position: relative;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 23px;
	font-size: 13px;
	letter-spacing: 0.02em;
	color: #828282;
	margin-bottom: 0px;
}
.bestprice-cards-container{
    position: relative;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
    justify-content: space-evenly;
}

.bestprice-card {
	margin-top: 50px;
	width: 380px;
	margin: auto;
	padding-right: 0px;
	height: 280px;
 /*flex: 0 0 33.3333%;*/
	 align-items: center;
	 margin-top: 50px;
}
.card-body{
	padding-top: 16px;
	padding-left: 0px;
}
.bestprice-card-img-top {
	width: 100%;
  height: auto;
  overflow: hidden;
	

}
.bestprice-card-img {
}.bestprice-card-img:hover {
	transition: all 0.5s ease;
    transform:scale(1.2);
    filter: brightness(64%);
}

.bestprice-image-text {
    position: absolute;
    width: 136px;
    height: 66px;
	margin-top: -66px;
    margin-left: 244px;
    background: rgba(45, 156, 219, 0.6);
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 32px;
	font-size: 22px;
	text-align: center;
	text-transform: uppercase;
	color: #FFFFFF;
	padding: 20px;

}.bestprice-card-img-top:hover .bestprice-image-text{
	bottom: -6px;
	opacity: 0;
}

.bestprice-card-title{
	position: absolute;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 23px;
	font-size: 16px;
	text-transform: uppercase;
	color: #4F4F4F;
}
.bestprice-card-text{
	position: absolute;
	margin-top: 30px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: 18px;
	font-size: 12px;
	text-transform: uppercase;
	color: #4F4F4F;
}

.stars {
	position: absolute;
	width: 120px;
	height: 16px;
    margin-left: 260px;
}
.review-counter {
	position: absolute;
    margin-left: 260px;
	margin-top: 30px;
	font-family: Roboto;
	font-style: normal;
	font-weight: 500;
	line-height: 18px;
	font-size: 12px;
	text-transform: lowercase;
	color: #BDBDBD;
}


@media (min-width: 320px) and (max-width: 500px){
	.bestprice-block{
		top:400px;
		left: 10px;
		width: calc(100vw - 20px);
		}
	.bestprice-title {
		left: 10px;
		font-size: 10vw;
   		width: 100vw;
	}
	.bestprice-text {
		width: auto;
		top: 30px;
  	 	line-height: 30px;
   		font-size: 4vw;
   		height: 100%;
	}
	.bestprice-card {
		width: 80vw;
		 flex:auto;
	}
	.bestprice-cards-container{
	   left:0px;
	   width: 95vw;
	}
	.bestprice-image-text {
	    position: relative;
	    float: right;
	}
	.bestprice-card-img-top {
	    width: 95vw;
	}
	.stars {
		position: relative;
	    float: right;
	}
	.review-counter {
	    position: relative;
	    float: right;
	    margin-top:15px;
	    right:63px;
	    margin-left:0px;
	}
}
