.getstarted-parallax-container {
    margin-top: 450px;
    position: relative;
    width: 100%;
    height: 330px;
    overflow: hidden;
}
.getstarted-parallax-image {
    width: 100vw;
    height: 500px; 
    background-size: cover;
    background-position: center;

}
.getstarted-parallax-children {
	height: 330px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.getstarted-information{
    position: relative;
    width: calc(100vw - 790px);
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.getstarted-parallax-text {
    margin-top: 20px;
    position: relative;
    font-family: Elsie;
    font-style: normal;
    font-weight: normal;
    line-height: 44px;
    font-size: 2.5vw;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 0px 11px rgba(0, 0, 0, 0.51);
}
.getstarted-button{
    position: relative;
    width: 175px;
    height: 54px;
    margin-top: 32px;
    margin-left:auto;
    margin-right: auto;
    background: #FFFFFF;
    border-radius: 5px;
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    font-size: 14px;
    text-align: center;
    border: none;
    color: #2D9CDB;
}.getstarted-button:hover{
    background: #EFEFEF;
}.getstarted-button:active{
    box-shadow:inset 0 0 5px  #C3C3C3;
}
@media (min-width: 320px) and (max-width: 500px){
      .getstarted-information{
        width: 95vw;
    }
        .getstarted-parallax-text {
        font-size: 5vw;
    }
    .getstarted-parallax-container {
        margin-top: 700px;
    }
}
@media (min-width: 501px) and (max-width: 991px){
      .getstarted-information{
        width: calc(100vw - 250px);
    }
        .getstarted-parallax-text {
        font-size: 3vw;
    }
}
@media (min-width: 991px) and (max-width: 1219px){
   .getstarted-information{
        width: calc(100vw - 550px);
    }
}
@media (min-width: 1220px) and (max-width: 1399px){
    .getstarted-information{
        width: calc(100vw - 700px);
    }
}
@media (min-width: 1400px){
    .getstarted-information{
        width: calc(100vw - 780px);
    }
}
@media (min-width: 1600px){
    .getstarted-information{
        width: calc(100vw - 900px);
    }
    .getstarted-parallax-text {
        line-height: 50px;
    }
}