.usp-block {
	position: absolute;
	background-color: #2D9CDB;
	margin-top: -60px;
	height: 100px;

}
.usp-items-container {
	display: flex;
    flex-direction: row;
    height: 48px;
    margin-left: 82px;
    margin-right: 82px;
    margin-top: 24px;
    margin-bottom: 24px;
    justify-content: space-evenly;
}

.usp-item{
	margin: auto;
}
.usp-item-image{
	position: relative;
    left: -70px;
    height: 48px;
}
.usp-item-text {
	position: absolute;
	top: 35px;
	width: 150px;
	font-family: Roboto;
	font-style: normal;
	font-weight: bold;
	line-height: normal;
	font-size: 14px;
	text-transform: uppercase;
	color: #FFFFFF;
}

@media (min-width: 320px) and (max-width: 991px){
	.usp-block {
		margin-top:0px; 
	    position: relative;
	    height: 400px;
	   	background-color: #ffffff;
		}

	.usp-items-container {
		flex-direction: column;
		height: 400px;
		margin-top: 30px;
		margin-bottom: 0px;
		}

	.usp-line-image {
		display: none;
	}

	.usp-item{
		margin-left: 30%;
	    margin-right: 30%;	
	}

	.usp-item-text{
		position: relative;
 		top: -35px;
 		/*width:100vw;*/
 		font-size: 3vw;
 		color: black;
	}
	.usp-item-image{
		 filter: brightness(0%);
	}
}

@media (min-width: 992px) and (max-width: 1219px){
	.usp-block {
	    left: 70px;
   		width: calc(100vw - 140px);
	}
	.usp-item-text {
	    width: 120px;
	    font-size: 1vw;
	}
	.usp-item-image {
	    left: -50px;
	}
	.usp-items-container {
	    margin-left: 20px;
	    margin-right: 20px;
	}
}

@media (min-width: 1220px) and (max-width: 1399px){
	.usp-block {
	    left: 70px;
   		width: calc(100vw - 140px);
	}
}

@media (min-width: 1400px){
	.usp-block {
		left: 165px;
   		width: calc(100vw - 330px);
	}
}