*:focus {outline:none !important}

.App {
  text-align: center;
}
.main-block {
    width:100vw;
    overflow-x: hidden;
}
.parallax-container {
    top: 0px;
    position: relative;
    width: 100%;
    height: 110vh;
    overflow: hidden;
}

.parallax-image {
    width: 100vw;
    height: 150vh; 
    background-size: cover;
    background-position: center;
}

.parallax-children {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    
    display: flex;
    align-items: center;
    justify-content: center;
}
.parallax-text {
    position: absolute;
    width: 50vw;
    height: 229px;
    left: 170px;
    top: 231px;

    font-family: Hiragino Mincho Pro;
    line-height: normal;
    font-size: 3vw;
    letter-spacing: 0.02em;
    text-transform: uppercase;

    color: #FFFFFF;
}

